<template>
  <div class="com-container">
    <div class="com-chart" ref="scatter_ref"></div>
    <div class="com-chart" ref="scatter_ref_test"></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      chartInstance: null,
      chartInstance_test: null,
      Data: [],
      Data_test: []
    }
  },
  mounted () {
    this.initChart()
    this.getData()
  },
  methods: {
    initChart () {
      this.chartInstance = this.$echarts.init(this.$refs.scatter_ref)
      this.chartInstance_test = this.$echarts.init(this.$refs.scatter_ref_test)
    },

    getData () {
      //   this.allData =
      const that = this
      var ws = new WebSocket('ws://42.193.1.142:8181')
      ws.onopen = function (e) {
        // console.log('成功連接')
      }
      ws.onmessage = async function (e) {
        // console.log(JSON.parse(e.data))
        // 增加數據
        // console.log(JSON.parse(e.data).data.payload.params.measure)
        that.Data.push([that.format(JSON.parse(e.data).timemills), JSON.parse(e.data).payload.params.measure])
        that.Data_test.push([that.format(JSON.parse(e.data).timemills), JSON.parse(e.data).payload.params.force_of_hx])
        // console.log(that.Data)

        that.updateChart()
      }

      this.updateChart()
    },

    updateChart () {
      const option = {
        xAxis: {
          type: 'time',
          name: '时间轴',
          boundaryGap: false, // 距离坐标原点是否有间隙
          axisLabel: { //  如果这个字段不设置，echarts会根据屏宽及横坐标数据自动给出间隔
            interval: 30, // 间隔长度，可自定义（如果是时间格式，echarts会自动处理）
            rotate: 40 // 横坐标上label的倾斜度
          }
        },
        yAxis: {
          type: 'value',
          name: 'measure',
          axisLine: {
            show: true
          }
        },
        series: [
          {
            symbolSize: 5,
            data: this.Data,
            type: 'scatter'
          }
        ]// ,
        // grid: {
        //   x: '10%',
        //   y: '10%',
        //   bottom: '3%',
        //   containLabel: true
        // }
      }
      this.chartInstance.setOption(option)

      const option_test = {
        xAxis: {
          type: 'time',
          name: '时间轴',
          boundaryGap: false, // 距离坐标原点是否有间隙
          axisLabel: { //  如果这个字段不设置，echarts会根据屏宽及横坐标数据自动给出间隔
            interval: 30, // 间隔长度，可自定义（如果是时间格式，echarts会自动处理）
            rotate: 40 // 横坐标上label的倾斜度
          }
        },
        yAxis: {
          type: 'value',
          name: 'force_of_hx',
          axisLine: {
            show: true
          }
        },
        series: [
          {
            symbolSize: 5,
            data: this.Data_test,
            type: 'scatter'
          }
        ]// ,
        // grid: {
        //   x: '10%',
        //   y: '10%',
        //   bottom: '3%',
        //   containLabel: true
        // }
      }
      this.chartInstance_test.setOption(option_test)
    },

    add0 (m) { return m < 10 ? '0' + m : m },

    format (shijianchuo) {
      // shijianchuo是整数，否则要parseInt转换
      var time = new Date(parseInt(shijianchuo))
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      // return this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s)
      return y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s)
    }
  }
}
</script>

